








import { Component, Vue } from 'vue-property-decorator'

@Component({})
export default class EmptyHome extends Vue {
  user = {
    id: '',
    email: '',
    first_name: '',
  }

  mounted() {
    this.getUserInfo()
  }

  async getUserInfo() {
    try {
      const res = await this.$store.dispatch('moduleAuth/onGetUserInfo')

      console.log('res: ', res)

      this.user = res.data
    } catch (error) {
      throw Error()
    }
  }
}
